import React, { useState } from 'react'
import { MapMarker } from 'react-kakao-maps-sdk'
const Marker = (props) => {
    const [isOpen, setIsOpen] = useState(false)
  return (
    <MapMarker //  
        onClick={() => setIsOpen(true)}
        position={{
          //   
          lat: props.lat,
          lng: props.lng,
        }}
        image={{
            src: "./img/react/mark.png", //  
            size: {
              width: 64,
              height: 69,
            }, //  
            options: {
              offset: {
                x: 27,
                y: 69,
              }, //  .       .
            },
          }}
      >{isOpen && (
        
            <div className="wrap bg-white p-3" style={{padding:"10px", marginTop: "-50px",backgroundColor: "white", border: "1px solid #000", borderRadius:"10px"}} onClick={() => setIsOpen(false)}
                    title="">
              <div className="info">
                <div className="title h4 text-primary" style={{fontSize:"20px", fontWeight:" bold"}}>
                    {props.name}
                </div>
                <div className="body">
                  <div className="desc">
                    <div className="ellipsis">
                      {props.addr}
                    </div>
                    <div className="jibun ellipsis">
                      () {props.useTime}<br/> () {props.chgerType}
                    </div>
                  </div>
                </div>
              </div>
            </div>
        )}
      </MapMarker>
  )
}

export default Marker