import { Map, MapMarker } from 'react-kakao-maps-sdk'
import LatLng from './LatLng'
// import './map.css'

const Maps = (props) => {
  let Viewwidth;
  let Viewheight;  
  if(props.ViewPort){
    Viewwidth= "100%";
    Viewheight="80vh";
  }else{
    Viewwidth= "70%";
    Viewheight="60vh";
  }
  return (
    <>
      <Map //   Container
        className='Map'
        center={{
          //  
          lat: props.lat,
          lng: props.lng,
        }}
        
        style={{
          //  
          float: "right",
          width: Viewwidth,
          height: Viewheight
        }}
        level={4} //   
      >
        <LatLng MyLat = {props.MyLat} MyLng={props.MyLng} />
        
        <MapMarker //  
          position={{ lat: props.MyLat, lng: props.MyLng }}
          image={{
            src: "./img/react/icons.gif", //  
            size: {
              width: 50,
              height: 50,
            }, //  
            options: {
              offset: {
                x: 27,
                y: 69,
              }, //  .       .
            },
          }}
        />
      </Map>
    </>
  )
}

export default Maps